ol.circles-list {
  list-style-type: none;
  list-style-type: decimal !ie;
  /*IE 7- hack*/
  margin: 0;
  margin-left: 4em;
  padding: 0;
  counter-reset: li-counter;
}
ol.circles-list > li {
  position: relative;
  margin-bottom: 50px;
  padding-left: 0.5em;
  min-height: 3em;
}
ol.circles-list > li:before {
  position: absolute;
  top: 0;
  left: -1.73em;
  width: 1.5em;
  height: 1.5em;
  font-size: 2.0em;
  line-height: 1.2;
  text-align: center;
  color: #f5f5f5;
  border: 3px solid #c5c5c5;
  border-radius: 50%;
  background-color: #464646;
  content: counter(li-counter);
  counter-increment: li-counter;
}
