.btn-scroll {
  position: relative;
  overflow: hidden;
  font-family: "Raleway", sans-serif !important;
  line-height: inherit !important;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -ms-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  font-size: 14px;
}
.btn-scroll[class*="fa-"]:before {
  font-family: FontAwesome;
}
.btn-scroll[class*="ti-"]:before {
  font-family: themify;
}
.btn-scroll:after {
  content: "";
  position: absolute;
  transition: all 0.3s ease 0s;
  z-index: -1;
}
.btn-scroll:before {
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  position: relative;
  text-transform: none;
  font-size: 100%;
  height: 100%;
  line-height: 2.5;
  position: absolute;
  transition: all 0.3s ease 0s;
  width: 100%;
}
.btn-scroll span {
  backface-visibility: hidden;
  display: inline-block;
  height: 100%;
  transition: all 0.3s ease 0s;
  width: 100%;
}
.btn-scroll.btn-scroll-top:hover span {
  -webkit-transform: translateY(300%);
  -moz-transform: translateY(300%);
  -ms-transform: translateY(300%);
  -o-transform: translateY(300%);
}
.btn-scroll.btn-scroll-top:hover:before {
  top: 0;
}
.btn-scroll.btn-scroll-top:before {
  left: 0;
  top: -100%;
}
.btn-scroll.btn-scroll-left:hover span {
  -webkit-transform: translateX(200%);
  -moz-transform: translateX(200%);
  -ms-transform: translateX(200%);
  -o-transform: translateX(200%);
}
.btn-scroll.btn-scroll-left:hover:before {
  left: 0;
}
.btn-scroll.btn-scroll-left:before {
  left: -100%;
  top: 0;
}
